/* Мобільне меню */
@media screen and (max-width: 767px) {
  .header-main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 480px;
    box-sizing: border-box;
    height: 58px;
    padding-top: 14px;
    padding-left: 20px;
    padding-right: 20px;
    margin-right: auto;
    margin-left: auto;
    background-color: #84a178;
  }

  .header-logo-icon {
    width: 48px;
    height: 44px;
  }

  .header-logo {
    width: 48px;
    height: 44px;
    background-color: transparent;
    border: none;
    padding: 0;
    line-height: 0;
  }

  .header-site-nav {
    display: none;
  }

  .header-button {
    display: none;
  }

  .header-mobile-menu-open {
    fill: #ffffff;
    background-color: transparent;
    border: none;
    padding: 0;
    line-height: 0;
  }

  .header-mobile-menu {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 999;
    top: 0;
    left: 0;
    background-color: #84a178;
    opacity: 0;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    visibility: hidden;
    pointer-events: none;
    transition: opacity 500ms linear, visibility 500ms linear;
  }

  .header-mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .header-mobile-menu-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
    padding: 0;
  }

  .header-logo-mobile {
    position: absolute;
    width: 48px;
    height: 44px;
    top: 20px;
    left: 20px;
    background-color: transparent;
    border: none;
    padding: 0;
    line-height: 0;
  }

  .header-logo-mobile-icon {
    width: 48px;
    height: 44px;
  }

  .header-mobile-menu-close-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: transparent;
    border: none;
    padding: 0;
    line-height: 0;
  }

  .header-site-nav-mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 104px;
    margin-bottom: 114px;
  }

  .header-item-mobile {
    margin-bottom: 32px;
  }

  .header-item-mobile:last-child {
    margin-bottom: 0px;
  }

  .header-nav-link-mobile {
    display: block;
    font-weight: 700;
    font-size: 32px;
    line-height: 1.2;
    color: #fcfcfc;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .header-nav-link-mobile:hover,
  .header-nav-link-mobile:focus {
    color: #131313;
  }

  .header-button-mobile {
    display: flex;
    align-items: center;
    width: 152px;
    height: 42px;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 36px;
    border-radius: 4px;
    border: transparent;
    color: #000000;
    background-color: #f8d24e;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    cursor: pointer;
    font-family: var(--primary-font);
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
      color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .header-button-mobile:hover {
    background-color: #0a8817;
  }

  .header-button-mobile:focus {
    background-color: #0a8817;
    color: #ffffff;
  }

  .header-button-mobile:disabled {
    background-color: #ababab;
  }

  .header-button-mobile-icon {
    margin-left: 4px;
  }
}

