:root {
  --primary-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
}

body {
  background-color: #84a178;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

.container {
  width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (max-width: 479px) {
  .container {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

html {
  cursor: url("cursor-auto-@1x.334be0f7.png"), pointer;
}

@supports (cursor: url("data:image/webp"), pointer) {
  html {
    cursor: url("cursor-auto-@1x.1dfb0f7a.webp"), pointer;
  }
}

a, button, textarea, input, svg, .swiper .mySwiper1 {
  cursor: url("cursor-pointer-@1x.6cc51ddb.png"), pointer;
}

@supports (cursor: url("data:image/webp"), pointer) {
  a, button, textarea, input, svg, .swiper .mySwiper1 {
    cursor: url("cursor-pointer-@1x.31ea3f01.webp"), pointer;
  }
}

