.products-item:before {
  content: " ";
  width: 200px;
  height: 200px;
  background-size: 200px 200px;
  border-radius: 100%;
  position: absolute;
  top: 11px;
  transform: translate(-50%, -50%);
}

.products-item-cream:before {
  background-color: rgba(156, 190, 143, .7);
  box-shadow: 0 5px 20px rgba(10, 136, 23, .25);
}

.products-item-coffee:before {
  background-color: rgba(248, 210, 78, .7);
  box-shadow: 0 5px 20px rgba(153, 114, 23, .25);
}

.products-item-milkshakes:before {
  background-color: rgba(213, 117, 63, .7);
  box-shadow: 0 6px 20px rgba(0, 0, 0, .25);
}

.products-item:after {
  content: " ";
  width: 200px;
  height: 200px;
  background-size: 200px 200px;
  border-radius: 100%;
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.products-item-cream:after {
  background-image: url("circl-icecream-1x.ddfc9f47.png");
}

