.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 520;
  transition: opacity var(--transition-dur-and-timing-fun);
  -webkit-transition: opacity var(--transition-dur-and-timing-fun);
  -moz-transition: opacity var(--transition-dur-and-timing-fun);
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.modal {
  opacity: 0;
  visibility: hidden;
  max-height: 1050px;
  height: 100%;
  z-index: 1030;
  text-align: center;
  pointer-events: none;
  transition: opacity var(--transition-dur-and-timing-fun);
  -webkit-transition: opacity var(--transition-dur-and-timing-fun);
  -moz-transition: opacity var(--transition-dur-and-timing-fun);
